.booking {
  width: 100%;
  height: 555px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 0;
  color: $blue-black;
  font-size: 18px;
  line-height: 27px;
  padding: 50px 163px;
  background: url("../img/booking-bg-desktop@1x.jpg") no-repeat 50% 50%/cover  transparent;

  @media (min-resolution: $retina-dppx) {

    background-image: url("../img/booking-bg-desktop@2x.jpg");
  }

  @media (max-width: $tablet-width) {
    height: 805px;
    flex-direction: column;
    justify-content: flex-start;
    padding: 100px 45px;
    align-items: center;
    background-image: url("../img/booking-bg-tablet@1x.jpg");

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/booking-bg-tablet@2x.jpg");
    }
  }

  @media (max-width: $mobile-width) {
    height: 760px;
    padding: 57px 15px 65px 15px;
    font-size: 16px;
    line-height: 22px;
    background-image: url("../img/booking-bg-mobile@1x.jpg");
    background-position: 54.5% 50%;

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/booking-bg-mobile@2x.jpg");
    }
  }
}

.booking__info {
  width: calc(100% - 463px);

  p {
    width: 65%;
    margin: 7px 0;
  }

  @media (max-width: $tablet-width) {
    width: 420px;
    text-align: center;
    margin-bottom: 48px;

    p {
      width: 90%;
      margin: 5px auto;
    }
  }

  @media (max-width: $mobile-width) {
    width: 100%;
    margin-bottom: 28px;

    p {
      width: 95%;
      max-width: 420px;
    }

    br {
      display: none;
    }
  }
}

.booking__title {
  font-weight: bold;
  font-size: 40px;
  line-height: 60px;
  margin: 0;

  @media (max-width: $tablet-width) {
    width: 100%;
    font-size: 34px;
    line-height: 28px;
    margin: 0 0 16px;
  }

  @media (max-width: $mobile-width) {
    font-size: 24px;
    line-height: 31px;
  }
}
